nav{
    background-color: rgb(11, 1, 65);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index:2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);

}

nav a{
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 0rem;
    display: inline-block;
}
/* nav a:hover{
    background: rgb(1, 60, 1);

} */
nav a:active{
    background:  var(--color-bg);
    color: var(--color-bg);
}